@import url('https://fonts.cdnfonts.com/css/amazon-ember');

.discount {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid red;
  gap: 100px;
  margin: 5px;
  font-family: 'Amazon Ember', sans-serif;
}

.one{
  font-size: xx-large;
}

.two{
  display: flex;
  align-items: center;
  font-size: small;
  margin-top: -40px;
}

.three{
  display: flex;
  font-size: larger;
  margin-top: -30px;
}

.red{
  color: red;
}

@media screen and (max-width: 600px) {
  .discount{
    gap: 10px;
    margin: 0;
  }
  .one{
    font-size: larger;
  }
  .two{
    font-size: small;
    margin-top: -30px;
  }
  .three{
    font-size: medium;
  }

}