
 .header-images-slider img {
   display: none;
   width: 100%;
   height: 70vh;
   object-fit: cover;
 }

 .box img {
   width: 25%;
   height: 200px;
   object-fit: cover;
   cursor: pointer;
 }

 .see {
   display: flex;
   justify-content: space-around;
   background-color: black;
   color: white;
   cursor: pointer;
 }

 .category {
   display: flex;
   justify-content: space-around;
 }


 @media screen and (max-width: 500px) {
   .box img {
     width: 50%;
   }

   .header-images-slider img{
     height: 40vh;
   }
 }

 @media screen and (min-width:500px) and (max-width: 800px) {
   .box img {
     width: 33.3333333333333%;
   }
   .header-images-slider img{
    height: 50vh;
  }

 }